const hymo_token_contract = "0xbf0f4fe93738fda5c7685b42d6362d3f0af23a62";
const staking_contract_ = "0x845a4B501155C54d8d180293BB5F88897663a4fB";

export default class BSCExplorer {
  async getWeb3() {
    if (window.ethereum) {
      const web3 = new Web3(window.ethereum);
      //alert(web3)
      try {
        // ask user permission to access his accounts
        await window.ethereum.request({ method: "eth_requestAccounts" });
        return web3;
      } catch (error) {
        throw error;
      }
    } else {
      throw new Error("Must install Metamask");
    }
  }

  async getWeb3_TEST() {
	const web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:8545"));
	return web3;
  }

  async getChainID() {
	return await this.web3.eth.getChainId();
  }

  async getAccount(idx) {
    const account = (await window.ethereum.request({ method: 'eth_accounts' }))[idx];
    return account;
  }

  setAccount(account) {
	  this.account = account;
  }

  async getHYMOContract() {
    const hymoTokenContract = new this.web3.eth.Contract(
      	HYMOTokenABI,
		hymo_token_contract,
      { from: await this.getAccount() }
    );
    return hymoTokenContract;
  }

  async getStakingContract() {
    const stake = new this.web3.eth.Contract(
      saleABI,
      staking_contract_,
      { from: await this.account }
    );
    //alert(stake)
    return stake;
  }

  // eslint-disable-next-line no-useless-constructor
  constructor() {
    this.init = false;
  }

  async initialize() {
    try {
		this.web3 = await this.getWeb3();
		this.account = await this.getAccount(0);
		this.HymoTokenContract = await this.getHYMOContract();
		this.StakeContract = await this.getStakingContract();
		this.init = true;
		return this;
	}
	catch (error) {
		throw error;
	}
  }


  async reloadContracts() {
	this.HymoTokenContract = await this.getHYMOContract();
    this.StakeContract = await this.getStakingContract();
  }



  async getBNBBalance() {
    return Units.convert(
      await this.web3.eth.getBalance(this.account),
      "wei",
      "eth"
    );
  }

  async hymoApprove(){

  		let qtd = 100000000;

  		await this.HymoTokenContract.methods.approve(staking_contract_, Units.convert(qtd, "eth", "wei")).send({ from: this.account});
  }


  async getTotalSupply() {

  		let t;
	  await this.StakeContract.methods.totalSupply().call({ from: this.account }).then( result => {
			 // 	alert(result)
	  	document.getElementById("tStake").innerHTML = result / Math.pow(10, 18);
		  BigNumber.config({ EXPONENTIAL_AT: 1e+9 })
		  document.getElementById("apr").innerHTML = new BigNumber(100000).multipliedBy(Math.pow(10, 18) ).dividedBy(result).dividedBy(0.5).multipliedBy(100);
	  });
  }

	async earned() {

		let t;
		await this.StakeContract.methods.earned(this.account ).call({ from: this.account }).then( result => {
			// 	alert(result)

			BigNumber.config({ EXPONENTIAL_AT: 1e+9 })
			document.getElementById("earnRate").innerHTML = parseFloat(result / Math.pow(10, 18));
		});
	}

	async getUserSupply() {

		let t;
		await this.StakeContract.methods.balanceOf(this.account).call({ from: this.account }).then( result => {
			// 	alert(result)
			document.getElementById("amStaking").innerHTML = result / Math.pow(10, 18);
		});
	}

	async time(){
		await this.StakeContract.methods.periodFinish().call({ from: this.account }).then( result => {

			let unix_timestamp = parseInt(result)
// Create a new JavaScript Date object based on the timestamp
// multiplied by 1000 so that the argument is in milliseconds, not seconds.
			var date = new Date(unix_timestamp * 1000);

			var day = date.getUTCDate()

			var month = date.getMonth();
// Hours part from the timestamp
			var hours = date.getHours();
// Minutes part from the timestamp
			var minutes = "0" + date.getMinutes();
// Seconds part from the timestamp
			var seconds = "0" + date.getSeconds();

// Will display time in 10:30:23 format
			var formattedTime = day+"/"+ month + " - "+ hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
			// 	alert(result)
			document.getElementById("fTime").innerHTML = formattedTime;
		});

	}

	async staken(amount) {

		let t;
		if(amount > 0) {
			await this.StakeContract.methods.stake(Units.convert(amount, "eth", "wei")).send({from: this.account}).then( result =>{
				document.getElementById("amStakeTo").innerHTML = "";
			})
		}else{
			alert("You need to stake more than 0 HYMO tokens")
		}

	}



	async exiting() {

		let t;
		await this.StakeContract.methods.exit().send({ from: this.account })

	}


}
