import { useState, useEffect } from "react";
import BSCExplorer from "./BSCExplorer";
import whitelist from './whitelist.json';
import BigNumber from "bignumber.js";


function App() {
  const [PAUAmount, setPAUAmount] = useState(0);
  const [BNBAmount, setBNBAmount] = useState(0);

  const [PAUBalance, setPAUBalance] = useState(0);

  const [tRate, setTotalRate] = useState(0);
  const [userRate, setUserStake] = useState(0);
  const [ti, setTi] = useState(0);
  const [earn, setEarn] = useState(0);
  const [BNBUSDTRate, setBNBUSDTRate] = useState(0);

  const [msg, setMsg] = useState({ type: '', msg: '' });

  const [chain, setChain] = useState(0);
  const [validChain, setValidChain] = useState(true);

  const [account, setAccount] = useState("");
  const [validAccount, setValidAccount] = useState(true);

  const [bsc, setBsc] = useState(new BSCExplorer());




  const onChainChange = async chainId => {

    setChain(parseInt(chainId, 16));
    if (parseInt(chainId, 16) !== 3) {
      console.error('You\'re not on BSC Mainnet!');
      setMsg({ type: "error", msg: "Please select BSC Mainnet!" });
      //setValidChain(false);
    }
    else {
      setMsg({ type: "", msg: "" });
      await bsc.reloadContracts();
      // setPAUBalance(await bsc.getPAUBalance());
   //   setValidChain(true);
    }
  }

  const onAccountsChange = accounts => {
    setAccount(accounts[0]);
    console.log(`Address changed to ${accounts[0]}`);
    bsc.setAccount(accounts[0]);
    if (whitelist.addresses.includes(accounts[0].toLowerCase())) {
      setMsg({ type: "success", msg: "You're whitelisted!"});
      setValidAccount(true);
    }
    else {
      setMsg({ type: "error", msg: `You're not whitelisted!`});
      setValidAccount(false);
    }
  }


  const msgStyle = () => {
    let classes = '';
    switch (msg.type) {
      case 'success':
        classes = "bg-green-300 py-2 px-3 rounded-lg border border-green-500 font-medium text-white"
        break
      case 'error':
        classes = "bg-red-300 py-2 px-3 rounded-lg border border-red-500 font-medium text-white"
        break
      case 'warning':
        classes = "bg-yellow-300 py-2 px-3 rounded-lg border border-yellow-500 font-medium text-white"
        break
      case 'info':
        classes = "bg-blue-300 py-2 px-3 rounded-lg border border-blue-500 font-medium text-white";
        break;
      default:
        classes = "";
    }

    return classes;
  }

  // Efeito ao iniciar o app

      async function func(){
        setBsc(await bsc.initialize());


        setBNBUSDTRate(parseFloat((await (await fetch("https://api.binance.com/api/v3/ticker/price?symbol=BNBUSDT")).json()).price).toFixed(2));

        // Check account
        let acc = await bsc.getAccount(0);
        setTotalRate(await bsc.getTotalSupply());
        setUserStake(await bsc.getUserSupply());
        setEarn(await bsc.earned());
        setTi(await bsc.time())
        setAccount(acc);
        console.log(`Address is ${acc}`);


        // Check chain
        let chainId = await bsc.getChainID();
        //alert(chainId + "," + "d")
        setChain(chainId);
        if (chainId !== 3) {
          setMsg({ type: "error", msg: "Please select BSC Mainnet!"});
          setValidChain(false);
         // throw new Error('Invalid chain - You\'re not on BSC Mainnet!');
        }


      }


      if (window.ethereum) {
        func();

        window.ethereum.on('chainChanged', onChainChange);
        window.ethereum.on('accountsChanged', onAccountsChange);
      }
      else {
        setValidAccount(false);
        setValidChain(false);
        setMsg({type: 'error', msg: 'Please install a Binance Smart Chain wallet!' });
      }

  async function approveToken(){
      if(chain === 3) {
          await bsc.hymoApprove()
      }else{
          alert("You are in the wrong chain!")
      }
  }

  async function stake(){
        if(chain === 3) {

            await bsc.staken(document.getElementById("amStakeTo").value)
        }else{
            alert("You are in the wrong chain!")
        }
  }

  async function unstakei(){
      if(chain === 3) {
          alert("You are unstaking tokens before the period ends, a penalty will be applied")
          await bsc.exiting()
      }else{
          alert("You are in the wrong chain!")
      }
  }

      async function reLoading(){
        window.location.reload();
      }
  /*async function tStaking(){
        if(bsc.getStakingContract().toString() !== "undefined")
          await bsc.getTotalSupply()
  }*/
  //tStaking()



  return (
    <div className="flex flex-col items-center justify-center w-full bg-gradient-to-br from-white via-yellow-50 to-white h-screen">
      <div className="App container shadow-lg rounded-2xl px-6 py-4 flex flex-col w-96 h-content bg-white">
        <div className="App">
         <p style={{textAlign : "center"}}><button onClick={() => reLoading()}>Reload page</button></p>
            <br/>
          <p>Account Connected: <span>{ account }</span></p><br/>
          <p>Amount staking: <span id="amStaking">{ userRate }</span></p><br/>
          <p>APR: <span id="apr">{tRate}</span>%</p><br/>
          <p>Total Staked: <span id="tStake">{tRate}</span></p><br/>
          <p>Earned: <span id="earnRate">{tRate}</span></p><br/>
            <p>Stake ends: <span id="fTime">{ti}</span></p><br/>
          <button onClick={() => approveToken()}>Approve</button>
          <p> <input  id="amStakeTo" placeholder="Amount to stake"/>
            <button onClick={() => stake()}>Stake</button></p>


          <button onClick={() => unstakei()}>UnStake All</button>
          <br/><br/>
          <p>How to test:</p>
          <p> 1 - Connect to the ropsten network</p>
          <p> 2 - Approve the Token to be user</p>
          <p> 3 - Stake x amount of tokens ( If you don't have send a message requesting)</p>
          <p> 4 - Unstake</p>
          <p> Note: You may need to reload by clicking in the reload page to update the values</p>
        </div>
      </div>
    </div>
  );
}

export default App;
